.spinner-wrapper{
    display: flex;
    height:100%;
    background-color: black;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.spinner-cube{
    position: absolute;
    width: 10px;
    height:10px;
}

.spinner-cube:nth-child(1){
    top: 0%;
    left: 0%;
    background-color: #a333c8; 
}
.spinner-cube:nth-child(2){
    top: 0%;
    right: 0%;
    background-color: #fbbd08; 
}
.spinner-cube:nth-child(3){
    bottom: 0%;
    left: 0%;
    background-color: #e03997;
}
.spinner-cube:nth-child(4){
    bottom: 0%;
    right: 0%;
    background-color: #2185d0; 
}

.spinner-man{
    position: relative;
    height: 150px;
    width: 150px;
}

.spinner-hair{
    height: 150px;
    width: 150px;
    background-color: gray;
    animation: hair 2s steps(1, end)  infinite;
    -webkit-animation: hair 2s steps(1, end)  infinite;
}

.spinner-hair2{
    position: absolute;
    top:-30px;
    left:20px;
    height: 25px;
    width: 110px;
    background-color: gray;
    animation: hair 2s steps(1) 1s infinite;
    -webkit-animation: hair 2s steps(1) 1s infinite;
}

.spinner-hair3{
    position: absolute;
    top:-50px;
    left:60px;
    height: 15px;
    width: 30px;
    background-color: gray;
    animation: hair 2s  infinite;
    -webkit-animation: hair 2s  infinite;
}

.spinner-head{
    position: absolute;
    bottom:0px;
    left: 20px;
    height: 110px;
    width: 110px;
    background-color: lightgrey;
}

.spinner-eye-left{
    position: absolute;
    left: 15px;
    top: 25px;
    height: 20px;
    width: 20px;
    background-color: white;
}

.spinner-eye-right{
    position: absolute;
    right: 15px;
    top: 25px;
    height: 20px;
    width: 20px;
    background-color: white;
}

.spinner-eye-left-pupil{
    position: absolute;
    left: 5px;
    top: 5px;
    height: 10px;
    width: 10px;
    background-color: black;
    border-radius: 50%;

}

.spinner-eye-right-pupil{
    position: absolute;
    left: 5px;
    top: 5px;
    height: 10px;
    width: 10px;
    background-color: black;
    border-radius: 50%;
}
.spinner-nose{
    position: absolute;
    right: 45px;
    top: 25px;
    height: 40px;
    width: 20px;
    background-color: Silver
}

.spinner-mouth{
    position: absolute;
    right: 30px;
    top: 70px;
    height: 30px;
    width: 50px;
    background-color: Gray;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.spinner-mouth-hole{
    position: absolute;
    height: 7px;
    width: 15px;
    background-color: Black;
    animation: mouth 1s infinite;
    -webkit-animation: mouth 1s  infinite;
}

@keyframes hair {
    0% { background-color: #2185d0; }
    20% { background-color: #a333c8; } 
    40% { background-color: #e03997; }
    60% { background-color: #fbbd08; }
    80% { background-color: #db2828; }
    100% { background-color: #21ba45;}
   }

@keyframes mouth {
0% { height:10px; width: 3px;}
10% { height:3px;width: 15px; }
20% { height:17px; width: 14px;}
30% { height:4px; width: 30px;}
40% { height:13px; width: 10px;}
50% { height:2px; width: 30px;}
60% { height:8px; width: 20px;}
70% { height:10px;width: 10px; }
80% { height:5px; width: 30px;}
90% { height:11px; width: 5px;}
100% { height:3px; width: 3px; }
}